@import url('https://fonts.googleapis.com/css2?family=Comfortaa:wght@300&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Comfortaa', cursive;
  color: rgb(97, 218, 251);
  outline: none !important;
}
*:focus,
*:active {
  outline: none !important;
}
body{
  background-color: #282c34;
}
.button{
  background: none;
  border: 1px solid rgb(97, 218, 251);
  /* margin: 5px 0; */
  outline: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
}
.buttonTrue{
  background: none;
  border: 1px solid rgb(92, 204, 0);
  color: rgb(79, 197, 0);
  /* margin: 5px 0; */
  outline: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
}
.buttonTrue:hover{
  background: rgba(79, 197, 0, 0.1);
}
.button:hover{
  background: rgba(97, 218, 251, 0.1);
}
.input{
  padding: 10px 5px;
  outline: none;
  font-size: 17px;
  background: none;
  border: none;
  width: 100%;
  text-align: left;
  display: block;
}
/* .input:focus {outline: none !important;}
.input::-moz-focus-inner {border: 0px !important;} */

ul{
  list-style-type:disc;
}

.myModal {
  z-index: 5;
  position: fixed;
  top: 0;
  left: 0;
  display: none;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.637);
  margin: 0 auto;
  /* overflow-y: scroll; */
}
.myModalContent {
  padding: 25px;
  background: rgba(78, 92, 119, 0.907);
  border-radius: 5px;
  overflow-y: scroll;
}
.myModal.active {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}