.ulWrap{
    // padding-left: 30px;
    color: rgb(241, 62, 62);
}
.answersItems{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10px;
    margin: 20px 0 30px;
    // max-width: 800px;
}
.answersItem{
    margin: 0 auto;
    // width: 96%;
    width: 100%;
    & button{
        text-align: left;
    }
}
.buttonAnswer{
    background: none;
    border: 1px solid rgb(97, 218, 251);
    width: 100%;
    // margin: 5px auto;
    outline: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
}
.question{
    text-align: center;
    font-size: 20px;
}
.questionContainer{
    max-width: 800px;
    margin: 0 auto;
    padding: 10px;
}