.navbar{
    margin: 0 0 30px;
    // height: 50px;
    background: rgb(24, 24, 24);
    display: flex;
    justify-content: start;
    overflow-x: scroll;
    scrollbar-width: none;
    // display: grid;
    // grid-template-columns: repeat(5,1fr);
    // gap: 10px;
    &::-webkit-scrollbar {
        display: none;
      }
}
.aWrap{
    text-align: center;
    border: 1px solid rgb(97, 218, 251);
    background: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    text-decoration: none;
    &:hover{
        background: rgba(97, 218, 251, 0.1);
    }
}
.a{
    text-decoration: none;
    text-align: center;
    min-width: 15%;
}