.paragraph{
    padding: 10px;
    font-size: 15px;
    font-weight: bold;
}
.true{
    @extend .paragraph;
    background: rgba(0, 190, 0, 0.3);
    color: rgb(28, 211, 28);
}
.true span{
    color: rgb(0, 255, 0);
}
.false{
    @extend .paragraph;
    background: rgba(241, 62, 62,0.3);
    color: rgb(255, 59, 59);
}
.false span{
    color: rgb(255, 83, 83);
}